___CSS_LOADER_EXPORT___.push([module.id, `body {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
}

#weatherForm {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
}

#reportDiv {
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

#castsWrapper {
  width: 98vw;
  max-width: 2000px;
  display: flex;
  flex-flow: row wrap;
  gap: 8px;
}

#nameInput {
  color: black;
}

form {
  display: flex;
  gap: 5px;
}

.cityNameDiv {
  font-size: xx-large;
}

#submit,
button {
  border: 1px solid transparent;
  border-radius: 5%;
  width: 95px;
  height: 30px;
  background-color: #33adff;
  font-size: 80%;
  font-weight: bold;
}

#submit {
  font-size: 100%;
}

button {
  width: 90px;
}

#locationInfo {
  display: flex;
  flex-direction: row;
  gap: 15px;
  align-items: center;
}

.daily {
  width: 200px;
  height: 200px;
  border: 2px solid black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  border-radius: 10%;
}
